
.LoadingAnimation {
  position: relative;
  overflow: hidden;
  opacity: 0.2;
  height: 100%;
}

.LoadingAnimation .Checkbox span {
  opacity: 1 !important;
}

.LoadingAnimationSwipe::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent 25%,
  white 70%,
    transparent 75%
  );
  -webkit-animation: LoadingAnimationSwipe 1.6s ease-in-out infinite;
          animation: LoadingAnimationSwipe 1.6s ease-in-out infinite;
}

@-webkit-keyframes LoadingAnimationSwipe {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0) skew(-20deg);
            transform: translate3d(-100%, 0, 0) skew(-20deg);
  }

  100% {
    -webkit-transform: translate3d(100%, 0, 0) skew(0);
            transform: translate3d(100%, 0, 0) skew(0);
  }
}

@keyframes LoadingAnimationSwipe {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0) skew(-20deg);
            transform: translate3d(-100%, 0, 0) skew(-20deg);
  }

  100% {
    -webkit-transform: translate3d(100%, 0, 0) skew(0);
            transform: translate3d(100%, 0, 0) skew(0);
  }
}

.Button {
  opacity: 0.9;
}

.Button:hover {
  opacity: 1;
}

.Button:active {
  opacity: 0.95;
  box-shadow: inset 0 0 8px rgba(0,0,0,0.3);
}

.Button:disabled {
  opacity: 0.8;
  box-shadow: none;
  box-shadow: initial;
  cursor: default;
}

